.catalog-article {
  margin: 0;
  display: grid;
  grid-template-areas: "picture header ."
    "picture table table"
    "button button button";
  grid-template-columns: 50% 2fr 1fr;
  align-items: center;


  @media (min-width: $tablet-width) {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    background-image: linear-gradient(to bottom, $color-background 16%, $color-main-blocks 16%);
    align-items: stretch;
    text-align: center;
    justify-content: center;
    padding-right: 36px;
    padding-left: 36px;
    padding-bottom: 40px;
  }
}

.catalog-article__button {
  @include font-font($font-family-extra, normal, normal, 16px, 20px);
  grid-area: button;
  max-width: 100%;
  padding-top: 10px;
  padding-bottom: 10px;

  @media (min-width: $tablet-width) {
    margin-top: 12px;
    margin-left: 2px;
  }

  @media (min-width: $desktop-width) {
    margin-top: 14px;
    margin-left: 1px;
  }
}

.catalog-article__title {
  align-items: center;

  color: $color-checkbox-text;
  text-transform: uppercase;

  margin-bottom: 12px;

  @include font-font($font-family-extra, normal, normal, 16px, 20px);

  @media (min-width: $tablet-width) {
    padding-top: 10px;

    @include font-font($font-family-extra, normal, normal, 20px, 24px);

    width: 130px;
  }
}

.catalog-article__table-head {
  text-align: left;

  @include font-font($font-family-main, normal, normal, 12px, 15px);

  @media (min-width: $tablet-width) {
    border-bottom: 1px solid $color-elements-inner;
    padding-bottom: 7px;
    padding-top: 8px;

    @include font-font($font-family-main, normal, normal, 16px, 16px);
  }
}

.catalog-article__table-data {
  text-align: right;

  @include font-font($font-family-main, normal, normal, 11px, 15px);

  @media (min-width: $tablet-width) {
    border-bottom: 1px solid $color-elements-inner;
    padding-bottom: 7px;
    padding-top: 8px;

    @include font-font($font-family-main, normal, normal, 16px, 16px);
  }
}

.catalog-article__table {
  margin-bottom: 20px;
  grid-area: table;
  border-collapse: collapse;

  @media (min-width: $desktop-width) {
    margin-top: 2px;
    margin-left: 3px;
  }
}


.catalog-article__link {
  color: $color-checkbox-text;

  @include link-reset();

  &:focus-visible {
    @include my-active();
  }
}

.catalog-article__picture {
  grid-area: picture;
  align-self: center;
  outline: 0;
  margin-top: 5px;

  @media (min-width: $tablet-width) {
    margin-top: 7px;
  }

  &:focus .catalog-article__image {
    @include my-focus();
  }

  &:active .catalog-article__image {
    @include my-active();
  }
}

.catalog-article__image {
  object-fit: contain;
  width: 140px;
  height: 112px;

  @media (min-width: $tablet-width) {
    width: 168px;
    height: 200px;
  }

  @media (min-width: $desktop-width) {
    width: 172px;
    height: 196px;
    margin-top: -5px;
    margin-left: 2px;
  }
}

.catalog-article__header {
  grid-area: header;

  @media (min-width: $tablet-width) {
    align-self: center;
  }

  @media (min-width: $desktop-width) {
    margin-top: 8px;
    margin-left: 5px;
  }

  &:focus .catalog-article__title {
    @include my-focus();
  }

  &:active .catalog-article__title {
    @include my-active();
  }
}

.catalog-article__span {
  @media (min-width: $tablet-width) {
    padding-left: 5px;
  }
}
